// ********************
// ANIMATIONS
// ********************

@keyframes showAlert {
    from {
        visibility: hidden;
        opacity: 0.0;
        transform: scale(0.5);
    }

    to {
        visibility: visible;
        opacity: 1.0;
        transform: scale(1.0);
    }
}

.is-shown {
    animation: showAlert ease-in-out 0.3s forwards;
}